import React from 'react';
import { Link } from 'react-router-dom';
import Sound from 'react-sound';

import audio from '../assets/win.mp3';
import win from '../assets/big-win@2x.png';
import back from '../assets/back.png';
import check from '../assets/check.png';
import dyamond from '../assets/dyamond.png';

class Complete extends React.Component {
  render() {
    return (
      <div className="page page-primary">
        <img className="win" src={win} />
        <h1 class="win-title">
          Well done!<br />You earned <br />a discount!
        </h1>
        <Sound
          url="https://www.zapsplat.com/wp-content/uploads/2015/sound-effects-21014/zapsplat_multimedia_game_retro_musical_warning_001_21521.mp3?_=1"
          playStatus={Sound.status.PLAYING}
        />
        <audio className="audio" controls autoPlay="autoplay">
          <source src="" type="audio/mpeg" />
          Your browser does not support the audio element.
        </audio>
      </div>
    );
  }
}

export default Complete;



