import React from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import logo from '../assets/logo.png';

export default () => (
  <Link to="/entry">
    Home
    <div className="home">
      <img src={logo} />
    </div>
  </Link>
);



