import React from 'react';
import { Link } from 'react-router-dom';

import icon1 from '../assets/list@2x.png';
import icon2 from '../assets/tocomplete@2x.png';
import icon3 from '../assets/winner@2x.png';
import mapicon from '../assets/map@2x.png';
import close from '../assets/close.png';
import challenges from '../data/challenges';

const items = [
  {
    distance: '2 Km',
    challenges: [challenges[0], challenges[1]],
  },
  {
    distance: '5 Km',
    challenges: [challenges[2], challenges[3]],
  },
  {
    distance: '15 Km',
    challenges: [challenges[0], challenges[1]],
    challenges: [challenges[2], challenges[3]],
  },
];

export default () => (
  <div className="page">
    <header class="list-header">
      <div class="chip">
        <img src={close} />
        <span>Schenna</span>
      </div>
      <Link to="/map">
        <img src={mapicon} />
      </Link>
    </header>
    <p class="desc">
      Complete the challenges<br /> and get rewards!
    </p>
    {items.map(({ distance, challenges }) => (
      <div>
        <div className="distance">{distance}</div>
        <div className="challenge-list">
          {challenges.map(({ image, title, link }) => (
            <Link className="challenge-item" to="/challenge">
              <img src={image} />
              <div>{title}</div>
            </Link>
          ))}
        </div>
      </div>
    ))}
    <div className="bottom-nav">
      <img src={icon1} />
      <img src={icon2} />
      <img src={icon3} />
    </div>
  </div>
);



