import React from 'react';
import { Link } from 'react-router-dom';

import bar2 from '../assets/img-1-big@2x.png';
import back from '../assets/back.png';
import check from '../assets/check.png';
import dyamond from '../assets/dyamond.png';

export default () => (
  <div className="section">
    <div>
      <Link to="/list">
        <img src={back} />
      </Link>
    </div>
    <p>Talk with the Bartender</p>
    <img className="challenge-img" src={bar2} />
    <div className="challenge-desc">
      <p>
        Go to the oldest bar in town. Ask the bartender to share the interesting
        history of the bar with you
      </p>
      <div className="info">
        <Link to="/complete">
          <div className="where">
            <img src={check} />
            <div>
              <div>
                <i>Lido Schenna</i>
              </div>
              <div>MERANO</div>
            </div>
          </div>
          <div className="where">
            <img src={dyamond} />
            <div>1</div>
          </div>
        </Link>
      </div>
    </div>
  </div>
);



